import React, { Component } from 'react'
import { 
    CourseSelectorStyle
} from './CourseSelectorStyle'
import Text from '../Text/Text'
import { withRouter } from 'react-router-dom'

import LessonModel from '../../models/lesson'
import { observer } from 'mobx-react'

class CourseSelector extends Component {

    constructor(props) {
        super()
        
        this.state = {
            active: true,
        }
    }    

    nextPath(path) {
        if(path !== this.props.location.pathname)
            this.props.history.push(path)
    }

    onAddLesson(lesson) {
        LessonModel.addLesson(lesson)
    }
 
    
    render() {
        let courses = [
            {
                name: "–ö–∞–∑–∞—Ö—Å—Ç–∞–Ω —Ç–∞—Ä–∏—Ö—ã",
                language: "üá∞üáø",
                code: "KHK"
            },
            {
                name: "–ò—Å—Ç–æ—Ä–∏—è –ö–∞–∑–∞—Ö—Å—Ç–∞–Ω–∞",
                language: "üá∑üá∫",
                code: "KZR"
            },
            {
                name: "–ú–∞—Ç–µ–º–∞—Ç–∏–∫–∞–ª–∫ —Å–∞—É–∞—Ç—Ç—ã–ª—ã–∫",
                language: "üá∞üáø",
                code: "MHR"
            }
        ]
        
        let course = courses.map((data, i) => (
            <div className='selector-item'
            onClick={() => this.onAddLesson(data)}
            key={i}>
                <Text size='default'>{data.name}</Text>
                <Text size='default'>{data.language}</Text>
            </div>
        ))
        
    
        return (
            <>  
                <CourseSelectorStyle>
                        {course}
                </CourseSelectorStyle>
            </>
        )
    }
}

export default withRouter(observer(CourseSelector))